import { NgModule } from '@angular/core';
import { ReservationZonesComponent } from './reservation-zones.component';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    component: ReservationZonesComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReservationZonesRoutingModule { }
