import { IReservationZone } from '@interfaces';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Observable, Subscription } from 'rxjs';
import { RestaurantService } from '@services';
import { RestuarantReservationZonePayload } from '@payloads';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, FormBuilder } from '@angular/forms';
import { ReservationZone, ResponseDetail, Amenities } from '@models';
import { mergeMap, map } from 'rxjs/operators';

@Component({
  selector: 'app-reservation-zones',
  templateUrl: './reservation-zones.component.html',
  styleUrls: ['./reservation-zones.component.scss']
})
export class ReservationZonesComponent implements OnInit, OnDestroy {
  reservationZonesForm: FormGroup = this.fb.group({
    zones: new FormControl([])
  });
  reservationZones: Array<ReservationZone> = [];
  currentReservationZones: Array<number>;
  restaurantId: number = parseInt(localStorage.getItem('restaurantId'), 10);
  restaurantSubscription = Subscription.EMPTY;
  amenitiesSubscription = Subscription.EMPTY;

  constructor(
    private fb: FormBuilder,
    private restaurantService: RestaurantService,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.restaurantSubscription = this.getRestaurantDetails(this.restaurantId)
      .pipe(
        mergeMap((response: ResponseDetail): any => {
          this.currentReservationZones = response.restaurant.zones
            .filter((pZone: IReservationZone) => pZone.active === true)
            .map((pZone: IReservationZone) => pZone.id);

          this.reservationZonesForm.patchValue({
            zones: this.currentReservationZones
          });

          return this.getReservationZones();
        })
      )
      .subscribe((response: any) => {
        this.createZonesObjects(response as Array<ReservationZone>);
      });
  }

  ngOnDestroy() {
    this.restaurantSubscription.unsubscribe();
    this.amenitiesSubscription.unsubscribe();
  }

  onSubmit(): void {
    const reservationZonesPayload = new RestuarantReservationZonePayload(
      this.reservationZonesForm
    );

    this.amenitiesSubscription = this.restaurantService
      .setRestaurantAmenities(
        reservationZonesPayload.toJson(),
        this.restaurantId
      )
      .subscribe(() => this.openSnackBar('Cambios guardados', 'Entendido'));
  }

  onZoneCheck(zone: IReservationZone): void {
    zone.active = !zone.active;

    let currentSelectedZones = this.reservationZonesForm.get('zones').value;

    currentSelectedZones = this.onZoneSelection(zone, currentSelectedZones);

    this.reservationZonesForm.patchValue({
      zones: currentSelectedZones
    });
  }

  private onZoneSelection(
    zone: IReservationZone,
    currentZones: Array<number>
  ): Array<number> {
    const isZoneSelected = currentZones.includes(zone.id);

    if (isZoneSelected) {
      const index = currentZones.indexOf(zone.id);

      currentZones.splice(index, 1);
    } else {
      currentZones.push(zone.id);
    }

    return currentZones;
  }

  private createZonesObjects(reservationZones: Array<ReservationZone>): void {
    const zonesOptions = reservationZones;

    zonesOptions.forEach(pZone => {
      const zoneObject = {
        name: pZone.name,
        id: pZone.id,
        active: false
      };

      const isActive = this.currentReservationZones.find(id => id === pZone.id);

      if (isActive) {
        zoneObject.active = true;
      }

      this.reservationZones.push(zoneObject);
    });
  }

  private getRestaurantDetails(id: number): Observable<ResponseDetail> {
    return this.restaurantService.getRestaurantDetail(id);
  }

  private getReservationZones(): Observable<Array<ReservationZone>> {
    return this.restaurantService.getAmenities().pipe(
      map((response: Amenities) => {
        const reservationZones: Array<ReservationZone> = response.zones.map(
          (pZone: IReservationZone) => {
            return new ReservationZone(pZone);
          }
        );

        return reservationZones;
      })
    );
  }

  private openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000
    });
  }
}
